<template>
  <div>
    <h2 class="main-title">내 친구 확인하기</h2>

    <!-- 로그인한 상태라면 사용자 정보 표시 -->
    <div v-if="isLoggedIn">
      <p>
        <strong>{{ userNickname }}</strong
        >, 안녕하세요!
      </p>
      <p><strong>이메일:</strong> {{ userEmail }}</p>
    </div>

    <!-- 회원(친구) 검색 폼 -->
    <form
      @submit.prevent="searchFriend"
      v-if="isLoggedIn"
      class="row g-3 align-items-center"
    >
      <div class="search-area">
        <div class="col-auto">
          <label for="friendSearch" class="form-label"
            >회원(친구) 검색 - 친구의 닉네임으로 검색할 수 있습니다</label
          >
        </div>
        <div class="col-auto">
          <div class="input-group">
            <input
              type="text"
              v-model="friendSearchQuery"
              class="form-control"
              id="friendSearch"
              required
            />
            <button type="submit" class="btn btn-primary btn-primary-search">검색</button>
          </div>
        </div>
      </div>
    </form>

    <!-- 검색된 친구 목록 -->
    <div v-if="searchedFriends.length > 0">
      <h3>검색된 친구 목록</h3>
      <ul>
        <li v-for="friend in searchedFriends" :key="friend.id">
          {{ friend.nickname }} ({{ friend.email }})
        </li>
        <!-- 친구 검색 결과가 있을 때 버튼을 눌렀을 때 모달 표시 -->
        <button @click="openModal">회원 목록 보기</button>
      </ul>
    </div>

        <!-- 모달 -->
    <div class="modal" v-if="showModal">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">회원 목록</h5>
            <button type="button" class="btn-close" @click="closeModal"></button>
          </div>
          <div class="modal-body">
            <!-- 회원 목록을 모달 창에 표시 -->
            <ul>
              <li v-for="member in memberList" :key="member.id">
                {{ member.nickname }} ({{ member.email }})
              </li>
            </ul>
         </div>
      </div>
    </div>

    <!-- 검색 결과가 없을 때의 메시지 -->
    <div v-if="searchedFriends.length === 0 && searchPerformed">
      <p>검색된 친구가 없습니다.</p>
    </div>

    <!-- 버튼 그룹 -->
    <div class="button-group">

      <button
        @click="selectTab('myFriends')"
        class="btn btn-tab"
        :class="{ 'btn-primary-tab': activeTab !== 'myFriends' }"
      >

        친구 목록
      </button>
      <button
         @click="selectTab('friendRequests')"
        class="btn btn-tab"
        :class="{ 'btn-primary-tab': activeTab !== 'friendRequests' }"
      >
        친구 상태 확인
      </button>
    </div>

    <!-- 내 친구 확인 탭 -->
    <div v-if="activeTab === 'myFriends'">
      <!-- 내 친구 확인에 관련된 데이터 및 컴포넌트를 표시 -->
      <h3>친구 목록</h3>
      <ul>
        <!-- <li v-for="friend in myFriends" :key="friend.id">
          {{ friend.nickname }} ({{ friend.email }})
        </li> -->
        <!-- 데이터 로딩 부분을 주석 처리합니다 -->
      </ul>
    </div>

    <!-- 수락 대기 목록 확인 탭 -->
    <div v-if="activeTab === 'friendRequests'">
      <!-- 수락 대기 목록 확인에 관련된 데이터 및 컴포넌트를 표시 -->
      <h3>친구 상태 확인</h3>
      <ul>
        <!-- <li v-for="request in friendRequests" :key="request.id">
          {{ request.nickname }} ({{ request.email }}) - 요청 상태: {{ request.status }}
        </li> -->
        <!-- 데이터 로딩 부분을 주석 처리합니다 -->
      </ul>
    </div>

    <!-- 데이터 표시 (미구현 상태) -->
    <div v-if="showData">
      <!-- 여기에 데이터를 표시하는 로직을 추가하세요 -->
      <p>Data will be displayed here.</p>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeTab: "myFriends", // 현재 활성화된 탭을 나타내는 변수
      myFriends: [], // 내 친구 목록 데이터
      friendRequests: [], // 수락 대기 목록 데이터
      showModal: false,
      memberList: [], // 이 줄을 추가하여 memberList를 선언합니다.
    };
  },
  computed: {
    isLoggedIn() {
      // 로그인 상태를 반환하는 로직 (실제 서버 연동이 필요합니다)
      return true; // 임시적으로 항상 로그인 상태로 설정합니다.
    },
    userNickname() {
      // 사용자 닉네임을 반환하는 로직 (실제 서버 연동이 필요합니다)
      return "사용자 닉네임"; // 임시적으로 사용자 닉네임을 설정합니다.
    },
    userEmail() {
      // 사용자 이메일을 반환하는 로직 (실제 서버 연동이 필요합니다)
      return "user@example.com"; // 임시적으로 사용자 이메일을 설정합니다.
    },
    searchedFriends() {
      // 검색된 친구 목록을 반환하는 로직 (실제 서버 연동이 필요합니다)
      return [];
    },
    searchPerformed() {
      // 검색이 수행되었는지 여부를 반환하는 로직 (실제 서버 연동이 필요합니다)
      return false;
    },
    showData() {
      // 데이터 표시 여부를 반환하는 로직 (실제 서버 연동이 필요합니다)
      return false;
    },
  },
  methods: {
    selectTab(tab) {
      // 탭을 변경할 때마다 호출되는 메서드
      this.activeTab = tab;
      // 해당 탭에 따라 데이터를 불러오거나 다른 동작을 수행할 수 있습니다.
      // 데이터 로딩 부분을 주석 처리했으므로, 이 메서드에서 필요한 로직을 추가하세요.
    },
    openModal() {
      // 모달 열기
      this.showModal = true;
      // 여기에서 데이터베이스와 연동하여 회원 목록을 가져오는 로직을 추가해야 합니다.
      // 임시로 더미 데이터를 사용하였습니다.
      this.memberList = [
        { id: 1, nickname: "회원1", email: "member1@example.com" },
        { id: 2, nickname: "회원2", email: "member2@example.com" },
        // ... 회원 목록 추가
      ];
    },
    closeModal() {
      // 모달 닫기
      this.showModal = false;
    },
  },
};
</script>

<style scoped>

.search-area {
  margin-top: 50px;
  margin-bottom: 50px;

}

.button-group {
  display: flex;
  justify-content: space-between;
