  background-color: #ffe0c7;
  border-color: #ffe0c7;
  color: #2c2b2b;
}

/* 버튼 공통 스타일 */
.btn-tab {
  padding: 10px 20px;
  background-color: #F5835D;
  border-color: #F5835D;
}

.btn-primary-tab {
  background-color: #ffe0c7;
  border-color: #ffe0c7;
}

.modal {
  display: none;
